// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import iView from 'iview';
import 'iview/dist/styles/iview.css';
import 'common/style/index.css';
import Index from './components/Index';
import employment from './components/employment';
import Home from './components/Home';
import Course from './components/Course';
import Tech from './components/tech';
import Class from './components/class';
import Us from './components/us';
import Start from './components/employmentStart';
import Page from './components/employmentPage';
import Teacher from './components/teacherChen';
import Active from './components/studentActive';
import Works from './components/stundentWorks';

Vue.use(VueRouter);
Vue.use(iView);

const routes = [{
  path: '/',
  component: Index,
  children:[
    {
      path: '/employment',
      component: employment ,
      children:[
        {
          path: '/Start',
          component: Start,
        },
        {
          path: '/Page',
          component: Page,
        }
      ]
    },
    {
      path: '/home',
      component: Home,
    },
    {
      path: '/course',
      component: Course,
    },
    {
      path: '/tech',
      component: Tech,
    },
    {
      path: '/class',
      component: Class,
      children:[{
        path: '/active',
        component: Active,
      },
      {
        path: '/works',
        component: Works,
      }
    ]
    },
    {
      path: '/us',
      component: Us,
    },
    {
      path: '/teacher',
      component: Teacher,
    },
  ]
},

]

const router = new VueRouter({
  routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  router,
  render: h => h(App)
}).$mount('#app-box')



