export default {
	components:{
	},
	mounted(){
      this.$router.push({ path: '/active' });
    },
	 methods:{
	onClick(name){
		if(!name){
			console.log('no name')
			this.$router.push({ path: `/` })
		}
		this.$router.push({ path: `${name}` });
	}
}
}



